'use client';

import { useEffect } from "react";
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import { initFirebase } from "@/lib/firebaseClient"; // Ajusta la ruta si es necesario

export default function LoginPage() {
  useEffect(() => {
    initFirebase();
    const doLogin = async () => {
      const auth = getAuth();
      await signInWithEmailAndPassword(auth, "juanhernandez@gmail.com", "123456");
      const user = auth.currentUser;
      if (user) {
        const token = await user.getIdToken();
        console.log(token); // <-- copia este string
      } else {
        console.error("No user is currently signed in.");
      }
    };
    doLogin();
  }, []);

  return (
    <div>
      Login page
    </div>
  );
}